import React, { Component } from 'react';
import {
    Form,
    Button
} from 'react-bootstrap';
import FormError from '../components/formError';
//Importing Firebase
import firebase from '../key_conections/firebase';
import { navigate, Link } from '@reach/router';
import {Trans, withTranslation} from 'react-i18next';

class LoginComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            errorMessage: ''
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(e) {
        //Getting form values and set it into state to pass
        //Getting Error Message
        const itemName = e.target.name;
        const itemValue = e.target.value;

        this.setState({[itemName]: itemValue});
    }

    handleSubmit(e) {
        console.log(this)
        //Save user information to firebase database
        let registrationInfo = {
            email: this.state.email,
            password: this.state.password
        }
        e.preventDefault();

        firebase
        .auth()
        .signInWithEmailAndPassword(
            registrationInfo.email,
            registrationInfo.password
        ).then(() => {
           navigate('/profile')
        })
        .catch(error => {
            if(error.message !== null) {
                this.setState({errorMessage: error.message})
            } else {
                this.setState({errorMessage: null})
            }
        })
    } 
        
    render() {
        return(
            <div className="container">
                <h1 className="text-center mt-4"><Trans>login</Trans></h1>
            <Form onSubmit={this.handleSubmit} className="form"> 
                {this.state.errorMessage !== null ? (
                <FormError Message={this.state.errorMessage}/>
                 ): null}
               
                    <Form.Group>
                        <Form.Label><Trans>EmailAddress</Trans></Form.Label>
                        <Form.Control 
                            type="email" 
                            placeholder="Enter email"
                            className=""
                            id="email"
                            name="email"
                            value={this.state.email}
                            onChange={this.handleChange}
                        />
                        <Form.Text className="text-muted">
                       <Trans>NeverShared</Trans>
                        </Form.Text>
                    </Form.Group>

                    <Form.Group>
                        <Form.Label><Trans>Password</Trans></Form.Label>
                        <Form.Control 
                            type="password" 
                            placeholder="Password"
                            className=""
                            id="password"
                            name="password"
                            value={this.state.password}
                            onChange={this.handleChange}
                            required
                        />
                    </Form.Group>
                    <Form.Text className="text-muted">
                        <Link to="/RecoveryPassword">
                            <Trans>ForgotPassword</Trans>
                        </Link>
                    </Form.Text>
  <div className="text-center">
    <Button variant="primary" type="submit">
        Login
    </Button>
  </div>
</Form>
</div>
        )
    }
}

const Login = withTranslation()(LoginComponent)
export default React.memo(Login);