import React, { Component } from 'react';
// Importing home css
import '../css/home.css';
//Importing Icons 
import { 
    BsCheckCircle,
    BsShieldShaded
} from 'react-icons/bs';
import { AiOutlineDollar } from 'react-icons/ai';
import { RiUserLine } from 'react-icons/ri';
import { FaLuggageCart, FaCloudMoon } from 'react-icons/fa';
import { MdAccessTime } from 'react-icons/md';
import { GoHome } from 'react-icons/go';
import { GiWaterSplash } from 'react-icons/gi';
//Importing bootstrap components
import {
    Row,
    Col,
    Container,
    Card, 
    Image
} from 'react-bootstrap';

//Importing Images
import honda from '../images/Fleets/Honda-Accord-Sport-1.5T-2020.jpg';
import Hyundai from '../images/Fleets/hyundai-sonata-y20-2017.jpg';
import ToyotaSienna from '../images/Fleets/toyota-sienna-limited-2018.jpg';
import ChryslerTC from '../images/Fleets/2016ChryslerTownCountry.png';
import ToyotaHighlander from '../images/Fleets/toyota-highlander20.png';
import ChevroletSub from '../images/Fleets/chevrolet-sub17.jpg';
import HyundaiH1 from '../images/Fleets/hyundai-h12020.jpg';
import ToyotaHiace from '../images/Fleets/toyo-hiace-20.png';
import HyundaiUniverse from '../images/Fleets/Hyundai-Universe.jpg';
//Importing Rents Images
import JetSki from '../images/rent-img/jetski.jpg'
import boatOne from '../images/rent-img/boat(1).jpg';
import boatTwo from '../images/rent-img/boat(2).jpg';

import carTransfer from '../images/extra-img/undraw_scooter_aia8.svg';

//Importing others Components
import ButtonTransfer from './button';
import ButtonRent from './rentBtn';

//Importing i18n, Translation
import { withTranslation, Trans} from 'react-i18next';


class HomeComponent extends Component {
      
    render() {
        return(
            <div>
                <Container className="mt-3 h70 container">
                    <Row>
                        <Col sm={8} className="frontHeading">
                            <h1>
                                <Trans>LKTransfers</Trans>
                            </h1>
                            <h2><Trans>GetTransfer</Trans></h2>
                            <h6>
                                <Trans>
                                    GETTHEBESTPRICE
                                </Trans>
                            </h6>
                            <div className="text-center mt-3 btns">
                                <ButtonTransfer />
                                <ButtonRent />
                            </div>
                        </Col>
                        <Col sm={4} className="front-img">
                            <Image src={carTransfer} fluid />
                        </Col>
                    </Row>
                </Container>
                <div className="text-center">
                    <Container className="mt-4 f-icons container">
                    <Row>
                        <Col sm={3}>
                            <BsCheckCircle className="icons"/>
                             <p>
                                <Trans>
                                    REFUND
                                </Trans>
                            </p>
                        </Col>
                        <Col sm={3}>
                            <MdAccessTime className="icons"/>
                            <p>
                                <Trans>
                                    FREETIME
                                </Trans>
                            </p>
                        </Col>
                        <Col sm={3}>
                            <BsShieldShaded className="icons"/>
                            <p>
                                <Trans>
                                    SECURE
                                </Trans>
                            </p>
                        </Col>
                        <Col sm={3}>
                            <AiOutlineDollar className="icons"/>
                            <p>
                                <Trans>
                                    HIDDENCOST
                                </Trans>
                            </p>
                        </Col>
                    </Row>
                    <Row>
                        <Col sm={3}>
                        <GoHome className="icons"/>
                            <p>
                                <Trans>
                                    RentVillas
                                </Trans>
                            </p>
                        </Col>
                        <Col sm={3}>
                        <FaCloudMoon className="icons"/>
                            <p>
                                <Trans>
                                    NightTour
                                </Trans>
                            </p>
                        </Col>
                        <Col sm={3}>
                            <GiWaterSplash className="icons" />
                            <p>
                                <Trans>
                                    Jetski
                                </Trans>
                            </p>
                        </Col>
                    </Row>
                    </Container>
                </div>

                <div>
                <h3 className="ml-2 mt-3 ourFleets">
                    <Trans>
                        OURFLEET
                    </Trans>
                </h3>
                    <div className="fleets">
                        <div className="fleets-img">         
                       
                        <Card className="card-fleet">
                            <Card.Img src={Hyundai} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X3 <br /> <FaLuggageCart /> X3</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        Economic
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className=" text-white card-fleet">
                            <Card.Img src={honda} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X3 <br /> <FaLuggageCart /> X3</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        business
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className=" text-white card-fleet">
                        <Card.Img src={ToyotaSienna} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X7 <br /> <FaLuggageCart /> X5</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        minivan
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className=" text-white card-fleet">
                        <Card.Img src={ChryslerTC} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X7 <br /> <FaLuggageCart /> X5</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        minivan
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className=" text-white card-fleet">
                        <Card.Img src={ToyotaHighlander} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X5 <br /> <FaLuggageCart /> X5</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        SUV
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className=" text-white card-fleet">
                        <Card.Img src={ChevroletSub} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X7 <br /> <FaLuggageCart /> X7</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        SUV
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className=" text-white card-fleet">
                        <Card.Img src={HyundaiH1} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X7 <br /> <FaLuggageCart /> X6</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        mbus
                                    </Trans>    
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className=" text-white card-fleet">
                        <Card.Img src={ToyotaHiace} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X12 <br /> <FaLuggageCart /> X12</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        mbus
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className=" text-white card-fleet">
                        <Card.Img src={HyundaiUniverse} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"><RiUserLine/> X50 <br /> <FaLuggageCart /> Up to  6.4 m<sup>3</sup></Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        bus
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </div>
                    </div>
                </div>

                {/* Rents */}
                <div>
                <h3 className="ml-2 mt-3 ourFleets">
                    <Trans>
                        RENT
                    </Trans>
                </h3>
                    <div className="fleets">
                        <div className="fleets-img">         
                       
                        <Card className="card-fleet">
                            <Card.Img src={JetSki} alt="Jet Ski Rentals" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"></Card.Title>
                                <Card.Text className="car-class_type">
                                    Jet Ski
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        <Card className="card-fleet">
                            <Card.Img src={boatOne} alt="boats Rentals" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"></Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        BOATS
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        <Card className="card-fleet">
                            <Card.Img src={boatTwo} alt="boats Rentals" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"></Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        BOATS
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        <Card className="card-fleet">
                            <Card.Img src={honda} alt="cars Rentals honda" />
                            <Card.ImgOverlay>
                                <Card.Title className="ct-ml"></Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        CARS
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const Home = withTranslation()(HomeComponent)
export default Home;