import React, { Component} from 'react';
import '../css/transferForm.css'
import mapStyle from '../css/mapStyle';
import {
  Row,
  Col,
  Card,
  Form, 
  Table
} from 'react-bootstrap';
//Importing currency format
import CurrencyFormat from 'react-currency-format';


//Importing component from react-select
import Select from 'react-select';
import makeAnimated from 'react-select/animated';
import seatOptions from '../key_conections/seatOptions';

//import DatePicker and TimePicker from 'react-date-picker';
//import TimePicker from 'react-time-picker';
import TimeKeeper from 'react-timekeeper';
//import TimeInput from 'material-ui-time-picker';
import DatePicker from 'react-date-picker';
//Importing Images
import honda from '../images/Fleets/Honda-Accord-Sport-1.5T-2020.jpg';
import Hyundai from '../images/Fleets/hyundai-sonata-y20-2017.jpg';
import ToyotaSienna from '../images/Fleets/toyota-sienna-limited-2018.jpg';
import ChryslerTC from '../images/Fleets/2016ChryslerTownCountry.png';
import ToyotaHighlander from '../images/Fleets/toyota-highlander20.png';
import ChevroletSub from '../images/Fleets/chevrolet-sub17.jpg';
import HyundaiH1 from '../images/Fleets/hyundai-h12020.jpg';
import ToyotaHiace from '../images/Fleets/toyo-hiace-20.png';
import HyundaiUniverse from '../images/Fleets/Hyundai-Universe.jpg';

//Importing Icon
import { RiUserLine } from 'react-icons/ri';
import { FaLuggageCart } from 'react-icons/fa';
import { AiOutlineMinusCircle, AiOutlinePlusCircle } from 'react-icons/ai';

//firebase 
import firebase from 'firebase';
import {Link, navigate} from '@reach/router';

//Importing i18n Translate
import { withTranslation, Trans } from 'react-i18next';
import emailjs from 'emailjs-com';

//import { DistanceMatrixService } from '@react-google-maps/api';
const { GoogleMap, DirectionsService, DirectionsRenderer, Autocomplete } = require("@react-google-maps/api");
// const ScriptLoaded = require("@react-google-maps/api").default;
const options = {
  styles: mapStyle
}
const styleMap = {  
  boxSizing: `border-box`,
  border: `1px solid transparent`,
  width: `100%`,
  height: `32px`,
  padding: `0 12px`,
  borderRadius: `3px`,
  boxShadow: `0 2px 6px rgba(0, 0, 0, 0.3)`,
  fontSize: `14px`,
  outline: `none`,
  textOverflow: `ellipses`,
  // position: "absolute",
  // left: "50%",
  // marginLeft: "-120px"  
}
const animatedComponents = makeAnimated();
// const { LoadScript } = require("./LoadScript");
// const ScriptLoaded = require("./docs/ScriptLoaded").default;


 class DirectionsComponent extends Component {
    constructor (props) {
      super(props)
  
      this.state = {
        response: null,
        travelMode: 'DRIVING',
        origin: '',
        destination: '',
        errorMessage: '',
        lat: 18.483402,
        lng: -69.929611,
        distance: '',
        distVal: 0,
        duration: null,
        time: '12:00pm',
        date: new Date(),
        carType: '',
        adults: 1,
        isChecked: false,
        carSeat: [],
        comment: '',
        mail: '',
        phoneNumber: '',
        price: 0,
        tipoCambio: 0,
        totalTipoCambio: 0,
        name: '',
        xChange: 58.25,
        terrenas: 0,
        ala: 0,
        juanDolio: 0,
        puertoPlata: 0,
        puntaCana: 0,
        soCa: 0,
        terPuntaCana: 0,
        bayahibe: 0,
        samana: 0,
        showTime: true
      }
  
      this.directionsCallback = this.directionsCallback.bind(this)
      this.getOrigin = this.getOrigin.bind(this)
      this.getDestination = this.getDestination.bind(this)
      this.onClick = this.onClick.bind(this)
      this.onMapClick = this.onMapClick.bind(this)
      //this.distanceMatrix = this.distanceMatrix.bind(this)
      //this.getUserLocation = this.getUserLocation.bind(this)
      //this.getCoors = this.getCoors.bind(this)
      // autocomplete
      this.autocomplete = null
      this.origen = null

      this.onLoad = this.onLoad.bind(this)
      this.onPlaceChanged = this.onPlaceChanged.bind(this)
      this.onOrigen = this.onOrigen.bind(this)
      this.onOrigenChanged = this.onOrigenChanged.bind(this)
      this.getCoors = this.getCoors.bind(this)

      // date Picker on change
      // this.onTimeChange = this.onTimeChange.bind(this);
      this.onDateChange = this.onDateChange.bind(this);
      //handle Cars selections
      this.handleCars = this.handleCars.bind(this);
      this.handleAddAdults = this.handleAddAdults.bind(this);
      this.handleLessAdults = this.handleLessAdults.bind(this);
      this.toggleChange = this.toggleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
      this.handleCarSeat = this.handleCarSeat.bind(this);
      this.handleInfo = this.handleInfo.bind(this);   
      this.handleChange = this.handleChange.bind(this);   
      this.puntoDist = this.puntoDist.bind(this);

      this.setTime = this.setTime.bind(this);
     
      //this.setShowTime = this.setShowTime.bind(this);
    }    


    handleSubmit(e) {
      e.preventDefault()
        const h = this.state.date;
        const hr = h.toDateString();
        
        let userTrans = {
          Partida: this.state.origin,
          Destino: this.state.destination,
          hora : this.state.time,
          fecha: hr,
          vehiculo: this.state.carType,
          pasajeros: this.state.adults,
          asientoNinos: this.state.carSeat,
          comentarios: this.state.comment,
          correo: this.state.mail,
          telefono: this.state.phoneNumber,
          Distancia: this.state.distance,
          precio: this.state.price,
          transaction: 'Pending',
          name: this.state.name,
          cancel: false
        }

        emailjs.send('lkTransfers@gmail.com', 'template_wlfj91m', userTrans , 'user_umWs3NNFDDPKpxc7iOQz1')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });

        let newTransferkey = firebase.database().ref().child('Transfers').push().key;

        var updates = {};
        updates['/Transfers/' + newTransferkey] = userTrans;
        updates['/user-Transfers/' + this.props.userID + '/' + newTransferkey] = userTrans;
      
        return firebase.database().ref().update(updates).then( 
          setTimeout(() => {
          navigate('/profile')
        }, 5000))
      }    
  
    directionsCallback (response) {  
      if (response !== null) {
        if (response.status === 'OK') {
          this.setState(
            () => ({
              response
            }
          )
        )       
        } else {
            console.log('response: ', response)
        }
      }
    }

      // AutoComplete selecting the origin
      onOrigen (origen) {    
        this.origen = origen
      }
      //AutoComplete Selecting the destinations
      onLoad (autocomplete) {
        console.log('autocomplete: ', autocomplete)    
        this.autocomplete = autocomplete
      }

      //AutoComplete Getting the origin and the destination places
      onOrigenChanged () {
        if (this.origen !== null) {
          console.log(this.origen.getPlace())
          this.setState({
            origin: this.origen.getPlace().formatted_address
          })
        } else {
          console.log('Origen Autocomplete is not loaded yet!')
        }
      }
    
      onPlaceChanged () {
        if (this.autocomplete !== null) {
          console.log(this.autocomplete.getPlace())
          this.setState({
            destination: this.autocomplete.getPlace().formatted_address
          })
        } else {
          console.log('Autocomplete is not loaded yet!')
        }
      }

      componentDidMount() {
          // const t = new Date();
          // const timeNow = t.toLocaleTimeString();
          this.setState({
            // time: timeNow,
            isChecked: false,
            carType: 'Hyundai, Economic Class' 
          })

          if (navigator.geolocation) {
              navigator.geolocation.getCurrentPosition(this.getCoors)
          } else {
              this.setState({ errorMessage: 'Geolocation is no supported by this browser' })
          }


        firebase.database().ref('price/').on('value', (priceVal) => {
          this.setState({
            terrenas: priceVal.val().Terrenas,
            ala: priceVal.val().AeropuertoLasAmericas,
            juanDolio: priceVal.val().JuanDolio,
            puertoPlata: priceVal.val().PuertoPlata,
            puntaCana: priceVal.val().PuntaCana,
            soCa: priceVal.val().SosuaCabarete,
            terPuntaCana: priceVal.val().TerrenasPuntaCana,
            bayahibe: priceVal.val().bayahibe,
            samana: priceVal.val().samana
          })
        })
      }    

      getCoors(position) {
          console.log(position.coords);
          this.setState({
              lat: position.coords.latitude,
              lng: position.coords.longitude
          })
      }

      onDateChange = date => this.setState({ date })
      // onTimeChange = time => this.setState({ time })
  
      getOrigin (ref) {
        this.origin = ref
      }
    
      getDestination (ref) {
        this.destination = ref
      }
  
      onClick(e) {
          e.preventDefault();
        if (this.origin.value !== '' && this.destination.value !== '') {
          this.setState(
              () => ({
                origin: this.origin.value,
                destination: this.destination.value
              })
          )
        }
      }

      handleCarSeat = (newValue, actionMeta) => {
        console.group('Value Changed');
        console.log(newValue);
        console.log(`action: ${actionMeta.action}`);
        console.groupEnd();
        for (const i of newValue) {
          console.log(i.value)
          this.setState({
            carSeat: i.value
          })
      }
      };
    
      onMapClick (...args) {
        console.log('onClick args: ', args)
      }

      puntoDist() {
        const o = this.state.origin;
        const des = this.state.destination;
        const {xChange} = this.props;
      
        if (((o.includes('Santo Domingo')) && (des.includes('Punta Cana'))) || ((o.includes('Punta Cana')) && (des.includes('Santo Domingo'))) ) {
          this.setState({
            price: this.state.puntaCana / (xChange)
          })
        } else if(((o.includes('Santo Domingo')) && (des.includes('Terrenas')))  || ((o.includes('Terrenas')) && (des.includes('Santo Domingo'))) ) {
          this.setState({
            price: this.state.terrenas / (xChange)
          })
        } else if(((o.includes('Santo Domingo')) && (des.includes('Puerto Plata')))  || ((o.includes('Puerto Plata')) && (des.includes('Santo Domingo'))) ) {
          this.setState({
            price: this.state.puertoPlata / (xChange)
          })
        } else if(((o.includes('Santo Domingo')) && (des.includes('Sosua Cabarete')))  || ((o.includes('Sosua Cabarete')) && (des.includes('Santo Domingo'))) ) {
          this.setState({
            price: this.state.soCa / (xChange)
          })
        } else if(((o.includes('Santo Domingo')) && (des.includes('Samana')))  || ((o.includes('Samana')) && (des.includes('Santo Domingo'))) ) {
          this.setState({
            price: this.state.samana / (xChange)
          })
        } else if(((o.includes('Santo Domingo')) && (des.includes('Aeropuerto las Americas')))  || ((o.includes('Aeropuerto las Americas')) && (des.includes('Santo Domingo'))) ) {
          this.setState({
            price: this.state.ala / (xChange)
          })
        } else if(((o.includes('Santo Domingo')) && (des.includes('bayahibe')))  || ((o.includes('Bayahibe')) && (des.includes('Santo Domingo'))) ) {
          this.setState({
            price: this.state.bayahibe / (xChange)
          })
        }
      }

      handleCars(e) {
         const cars = e.currentTarget.dataset.tag;
         console.log(cars);
          this.setState({
              carType: cars
            })
          //const gPrice = 250;
          const d = this.state.distVal/1000;
        

          console.log(d);
        if ( cars === 'Hyundai, Economic Class') {
          const convertionValue = .651;          
          const totalP = d*convertionValue;
          this.setState({
            price: totalP
          })
          this.puntoDist();

        } else if( cars === 'Honda, Business Class') {
          const convertionValue = .6518;
          const totalPrice = d*convertionValue;
          this.setState({
            price: totalPrice
          })
          this.puntoDist()
        } else if( cars === 'Toyota Sienna, Minivan Class') {
          const convertionValue = .722;
          const totalPrice = d*convertionValue;
          this.setState({
            price: totalPrice
          })
          this.puntoDist()
        } else if ( cars === 'Chrysler Town and Country, Minivan Class') {
          const convertionValue = .7225;
          const totalPrice = d*convertionValue;
          this.setState({
            price: totalPrice
          })
          this.puntoDist()
        } else if ( cars === 'Toyota Highlander, Jeep Class') {
          const convertionValue = .7023;
          const totalPrice = d*convertionValue;
          this.setState({
            price: totalPrice
          })
          this.puntoDist()
        } else if ( cars === 'Chevrolet Suburban, Jeep Class') {
          const convertionValue = .7033;
          const totalPrice = d*convertionValue;
          const FinalPrice = ((5 * totalPrice)/2) + totalPrice;
           this.setState({
             price: FinalPrice
           })
           this.puntoDist()
        } else if ( cars === 'Hyundai H1, Microbus Class') {
          const convertionValue = .9166;
          const totalPrice = d*convertionValue;
          this.setState({
            price: totalPrice
          })
          this.puntoDist()
        } else if ( cars === 'Toyota Hiace, Microbus Class') {
          const convertionValue = .9168;
          const totalPrice = d*convertionValue;
          this.setState({
            price: totalPrice
          })
          this.puntoDist()
        } else if ( cars === 'Hyundai Universe, Autobus Class') {
          const convertionValue = 2.532;
          const totalPrice = d * convertionValue;
          this.setState({
            price: totalPrice
          })
          this.puntoDist()
        }
      }
      
      handleAddAdults() {
        const x = this.state.adults + 1
        this.setState({
          adults: x
        })
        if(this.state.adults > 1 && this.state.adults < 3) {
          const adPrice = this.state.price + 10
          this.setState({
            price: adPrice
          })
        }
      }

      handleLessAdults() {
        const x = this.state.adults - 1
        this.setState({
          adults: x
        })
      }

      handleInfo(e) {
        const itemName = e.target.name;
        const itemValue = e.target.value;

        this.setState({[itemName]: itemValue})
      }

      toggleChange() {
        this.setState({
          isChecked: !this.state.isChecked,
        });
      }

      handleChange(event) {
        const {xChange} = this.props;
        this.setState({tipoCambio: event.target.value});
        const tc = new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange);
        const pc = this.state.price;
        const firstTC = this.state.tipoCambio

        console.log(firstTC);
      
        if (firstTC === 'RD') {
          const dp = pc * tc;
          this.setState({
            totalTipoCambio: dp
          })
        } else if (firstTC === 'US') {
          this.setState({
            totalTipoCambio: 0
          })
        }
      }

      onDateChange = date => this.setState({ date })

      setTime(newTime) {
       this.setState({
         time: newTime.formatted12
       })
    }
  
    render () {
      const {xChange} = this.props;
      // const [time, setTime] = useState('12:34pm')
      // const [showTime, setShowTime] = useState(true)
  

      return (
        <div className='map'>
          <div className='map-settings'>
            <hr className='mt-0 mb-3' />
  
            <div className='row destination'>
              <div className='col-md-6 col-lg-4'>
                <div className='form-group'>
                  <label htmlFor='ORIGIN'>
                    <Trans>Origen</Trans>
                  </label>
                  <br />
                  <Autocomplete
                    onLoad={this.onOrigen}
                    onPlaceChanged={this.onOrigenChanged}
                  >
                    <input
                      type="text"
                      placeholder="Origen"
                      style={styleMap}
                      ref ={ this.getOrigin }
                    />
                  </Autocomplete>
                </div>
              </div>
  
              <div className='col-md-6 col-lg-4'>
                <div className='form-group'>
                  <label htmlFor='DESTINATION'>
                    <Trans>
                    Destination
                    </Trans>
                  </label>
                  <br />
                  <Autocomplete
                    onLoad={this.onLoad}
                    onPlaceChanged={this.onPlaceChanged}
                  >
                    <input
                      type="text"
                      placeholder="Destination"
                      style={styleMap}
                      ref = {this.getDestination}
                    />
                  </Autocomplete>
                </div>
              </div>
            </div>
          </div>
  
          <div className='map-container'>
            
            <GoogleMap
              // required
              id='direction'
              // required
              mapContainerStyle={{
                height: '400px',
                width: '100%'
              }}
              // required
              zoom={14}
              // required
              center={{
                lat: this.state.lat,
                lng: this.state.lng
              }}

              options = { options }
              // optional
              onClick={this.onMapClick}
              // optional
              onLoad={map => {
                console.log('DirectionsRenderer onLoad map: ', map)
              }}
              // optional
              onUnmount={map => {
                console.log('DirectionsRenderer onUnmount map: ', map)
              }}
            >
              {
                (
                  this.state.destination !== '' &&
                  this.state.origin !== ''
                ) && (
                  <DirectionsService
                    // required
                    options={{ 
                      destination: this.state.destination,
                      origin: this.state.origin,
                      travelMode: this.state.travelMode
                    }}
                    // required
                    callback={this.directionsCallback}
                    // optional
                    onLoad={directionsService => {
                      console.log('DirectionsService onLoad directionsService: ', directionsService)
                    }}
                    // optional
                    onUnmount={directionsService => {
                      console.log('DirectionsService onUnmount directionsService: ', directionsService)
                    }}
                  />
                )
              }
  
              {
                this.state.response !== null && (
                  <DirectionsRenderer
                    // required
                    options={{ 
                      directions: this.state.response
                    }}
                    // optional
                    onLoad={directionsRenderer => {
                      console.log('DirectionsRenderer onLoad directionsRenderer: ', directionsRenderer)
                      console.log(directionsRenderer.directions.routes[0].legs[0].distance)
                      this.setState({
                        distance: directionsRenderer.directions.routes[0].legs[0].distance.text,
                        duration: directionsRenderer.directions.routes[0].legs[0].duration.text,
                        distVal: directionsRenderer.directions.routes[0].legs[0].distance.value
                      })
                    }}
                    // optional
                    onUnmount={directionsRenderer => {
                      console.log('DirectionsRenderer onUnmount directionsRenderer: ', directionsRenderer)
                    }}
                  />
                )
              } 
            </GoogleMap> 
            { this.state.duration !== null ? (
            <div className="dist-time">
              <Row>
                <Col xs={12} sm={6}>
                   <span>
                     <Trans>Distance</Trans>: {this.state.distance}</span>
                </Col>
                <Col xs={12} sm={6}>
                   <span>
                     <Trans>Durations</Trans>: {this.state.duration }</span>
                </Col>
              </Row>
            </div>
            ) : null}

            <div className="table mt-4">
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th colSpan="5" className="text-center">
                  <Trans>ServPers</Trans>
                  </th>
                </tr>
                <tr>
                  <th>#</th>
                  <th><Trans>From</Trans></th>
                  <th><Trans>To</Trans></th>
                  <th>RD$</th>
                  <th>US$</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>1</td>
                  <td>Santo Domingo</td>
                  <td>Punta Cana</td>
                  <td>RD$ {this.state.puntaCana}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.puntaCana / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
                <tr>
                <td>2</td>
                  <td>Santo Domingo</td>
                  <td>Terrenas</td>
                  <td>RD$ {this.state.terrenas}</td>
                  <td>US${new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.terrenas / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
                <tr>
                <td>3</td>
                  <td>Santo Domingo</td>
                  <td>Puerto Plata</td>
                  <td>RD$ {this.state.puertoPlata}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.puertoPlata / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
                <tr>
                <td>4</td>
                  <td>Santo Domingo</td>
                  <td>Sosua Cabarete</td>
                  <td>RD$ {this.state.soCa}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.soCa / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
                <tr>
                <td>5</td>
                  <td>Terrenas</td>
                  <td>Punta Cana</td>
                  <td>RD$ {this.state.terPuntaCana}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.terPuntaCana / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
                <tr>
                <td>6</td>
                  <td>Santo Domingo</td>
                  <td>Saman</td>
                  <td>RD$ {this.state.samana}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.samana / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
                <tr>
                <td>7</td>
                  <td>Santo Domingo</td>
                  <td>Aeropuerto Amricas</td>
                  <td>RD$ {this.state.ala}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.ala / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
                <tr>
                <td>8</td>
                  <td>Santo Domingo</td>
                  <td>Juan Dolio</td>
                  <td>RD$ {this.state.juanDolio}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.juanDolio / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
                <tr>
                <td>8</td>
                  <td>Santo Domingo</td>
                  <td>Bayahibe</td>
                  <td>RD$ {this.state.bayahibe}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(this.state.bayahibe / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange + 1))}</td>
                </tr>
              </tbody>
              <thead>
                <tr>
                  <th colSpan="5" className="text-center">
                  <Trans>ServPersMore</Trans>
                  </th>
                </tr>
                <tr>
                  <th>#</th>
                  <th><Trans>From</Trans></th>
                  <th><Trans>To</Trans></th>
                  <th>RD$</th>
                  <th>US$</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>1</td>
                  <td>Santo Domingo</td>
                  <td>Punta Cana</td>
                  <td>RD$ {this.state.puntaCana + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.puntaCana + 800) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
                <tr>
                <td>2</td>
                  <td>Santo Domingo</td>
                  <td>Terrenas</td>
                  <td>RD$ {this.state.terrenas + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.terrenas + 800 ) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
                <tr>
                <td>3</td>
                  <td>Santo Domingo</td>
                  <td>Puerto Plata</td>
                  <td>RD$ {this.state.puertoPlata + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.puertoPlata + 800 ) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
                <tr>
                <td>4</td>
                  <td>Santo Domingo</td>
                  <td>Sosua Cabarete</td>
                  <td>RD$ {this.state.soCa + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.soCa + 800 ) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
                <tr>
                <td>5</td>
                  <td>Terrenas</td>
                  <td>Punta Cana</td>
                  <td>RD$ {this.state.terPuntaCana + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.terPuntaCana + 800 ) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
                <tr>
                <td>6</td>
                  <td>Santo Domingo</td>
                  <td>Saman</td>
                  <td>RD$ {this.state.samana + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.samana + 800 ) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
                <tr>
                <td>7</td>
                  <td>Santo Domingo</td>
                  <td>Aeropuerto Amricas</td>
                  <td>RD$ {this.state.ala + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.ala + 800 ) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
                <tr>
                <td>8</td>
                  <td>Santo Domingo</td>
                  <td>Juan Dolio</td>
                  <td>RD$ {this.state.juanDolio + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.juanDolio + 800 ) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
                <tr>
                <td>8</td>
                  <td>Santo Domingo</td>
                  <td>Bayahibe</td>
                  <td>RD$ {this.state.bayahibe + 800}</td>
                  <td>US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format((this.state.bayahibe + 800 ) / new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange))}</td>
                </tr>
              </tbody>
            </Table>
            </div>
            
            <div className="dateTime">
              <Row>
                <Col xs="12" sm="6" className="mt-3 mb-3">
                  <label htmlFor="">
            <Trans>Date</Trans>   
                  </label> {' '}    
                  <DatePicker
                  onChange = {this.onDateChange}
                  value = { this.state.date }
                  ></DatePicker>
                </Col>
                <Col xs="12" sm="6" className="mt-3 mb-3">
                  <label htmlFor="">
            <Trans>Time</Trans>
                  </label> {' '}
                  <div className="text-center">
            {this.state.showTime &&
                <TimeKeeper
                    time={this.state.time}
                    onChange={this.setTime}
                    onDoneClick={() => {this.setState({showTime: false})}}
                    switchToMinuteOnHourSelect
                    value={this.state.time}
                />
            }<br/>
            <span><Trans>SelectedTime</Trans>{' '} {this.state.time}</span> <br />
            {!this.state.showTime &&
                <button className="btn btn-danger" onClick={() => {this.setState({ showTime: true})}}><Trans>ShowTime</Trans></button>
            }
            </div>
        
                </Col>
              </Row>
            </div>

            {//cars list
            }
            <div className="fleets">
              <h6><Trans>selectCar</Trans></h6>
                        <div className="fleets-img">
                        <button 
                        value="Hyundai, Economic class"
                        data-tag="Hyundai, Economic Class"
                        onClick={this.handleCars}>
                        <Card className="card-fleet">
                            <Card.Img src={Hyundai} alt="Car image"/>
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X3 <br /> <FaLuggageCart /> X3</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        Economic
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>
                        <button
                        value="Honda, Business Class"
                        data-tag="Honda, Business Class"
                        onClick={this.handleCars}>
                        <Card className=" text-white card-fleet">
                            <Card.Img src={honda} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X3 <br /> <FaLuggageCart /> X3</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        business
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>

                        <button
                        value = "Toyota Sienna, MInivan Class"
                        data-tag="Toyota Sienna, Minivan Class"
                        onClick={this.handleCars}>
                        <Card className=" text-white card-fleet">
                        <Card.Img src={ToyotaSienna} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X7 <br /> <FaLuggageCart /> X5</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        minivan
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>

                        <button
                        value="Chrysler Town and Country, Minivan Class"
                        data-tag="Chrysler Town and Country, Minivan Class"
                        onClick={this.handleCars}>
                        <Card className=" text-white card-fleet">
                        <Card.Img src={ChryslerTC} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X7 <br /> <FaLuggageCart /> X5</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        minivan
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>

                        <button
                        value = 'Toyota Highlander, Jeep Class'
                        data-tag="Toyota Highlander, Jeep Class"
                        onClick={this.handleCars}>
                        <Card className=" text-white card-fleet">
                        <Card.Img src={ToyotaHighlander} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X5 <br /> <FaLuggageCart /> X5</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        SUV
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>

                        <button
                        value ="Chevrolet Suburban, Jeep Class"
                        data-tag="Chevrolet Suburban, Jeep Class"
                        onClick={this.handleCars}>
                        <Card className=" text-white card-fleet">
                        <Card.Img src={ChevroletSub} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X7 <br /> <FaLuggageCart /> X7</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        SUV
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>

                        <button
                        value ="Hyundai H1, Microbus Class"
                        data-tag="Hyundai H1, Microbus Class"
                        onClick={this.handleCars}>
                        <Card className=" text-white card-fleet">
                        <Card.Img src={HyundaiH1} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X7 <br /> <FaLuggageCart /> X6</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        mbus
                                    </Trans>
                                  </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>

                        <button
                        value ="Toyota Hiace, Microbus Class"
                        data-tag="Toyota Hiace, Microbus Class"
                        onClick={this.handleCars}>
                        <Card className=" text-white card-fleet">
                        <Card.Img src={ToyotaHiace} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X12 <br /> <FaLuggageCart /> X12</Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        mbus
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>

                        <button
                        value="Hyundai Universe, Autobus Class"
                        data-tag="Hyundai Universe, Autobus Class"
                        onClick={this.handleCars}>
                        <Card className=" text-white card-fleet">
                        <Card.Img src={HyundaiUniverse} alt="Card image" />
                            <Card.ImgOverlay>
                                <Card.Title><RiUserLine/> X50 <br /> <FaLuggageCart />  Up to  6.4 m<sup>3</sup></Card.Title>
                                <Card.Text className="car-class_type">
                                    <Trans>
                                        bus
                                    </Trans>
                                </Card.Text>
                            </Card.ImgOverlay>
                        </Card>
                        </button>
                        </div>
                    </div>

                    <div>
                      <Row>
                        <Col xs={12} sm={6}>
                        <h6>
                          <Trans>Adults</Trans>
                        </h6>
                    <Row className="text-center adults">
                      <Col className="plus-less">
                      <button 
                      onClick={this.handleLessAdults}
                      >
                      <AiOutlineMinusCircle />
                    </button>
                      </Col>
                      <Col>
                      <p> {this.state.adults} </p>
                      </Col>
                      <Col className="plus-less">
                      <button 
                      onClick={this.handleAddAdults}
                      >
                      <AiOutlinePlusCircle />
                    </button>
                      </Col>
                    </Row>
                        </Col>
                        <Col xs={12} sm={6} className="m-auto text-center">
                        <label>
                        <input 
                          type="checkbox"
                          className="kid-chair"
                          checked={this.state.isChecked}
                          onChange={this.toggleChange}
                        />
                        <Trans>KIDCHAIR</Trans>
                      </label>
                        </Col>
                      </Row>
                    </div>
                    { this.state.isChecked !== false && (
                      <div>
                        <Select
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        defaultValue={[seatOptions[0]]}
                        isMulti
                        options={seatOptions}
                        ref = { [this.getCarSeat] }
                        onChange={this.handleCarSeat}
                      />
                     </div>
                    )}
                    <div>
                    <Form.Group controlId="exampleForm.ControlTextarea1">
                      <Form.Label>
                        <Trans>Comment</Trans>
                      </Form.Label>
                      <Form.Control 
                      as="textarea" 
                      name="comment"
                      rows="3" 
                      value={this.state.comment}
                      onChange={this.handleInfo}
                      />
                    </Form.Group>
                    </div>
                    <div>
                    <Form.Group controlId="formBasicName">
                    <Form.Label>
                      <Trans>Name</Trans>
                    </Form.Label>
                    <Form.Control 
                    type="name" 
                    name="name"
                    placeholder="Enter your Name"
                    value={this.state.name}
                    onChange={this.handleInfo} 
                    required/>
                  </Form.Group>
                  
                    <Form.Group controlId="formBasicEmail">
                    <Form.Label>
                      <Trans>EmailAddress</Trans>
                    </Form.Label>
                    <Form.Control 
                    type="email" 
                    name="mail"
                    placeholder="Enter email"
                    value={this.state.mail}
                    onChange={this.handleInfo} 
                    required/>
                  </Form.Group>

                  <Form.Group controlId="formBasicPassword">
                    <Form.Label>
                      <Trans>PhoneNumber</Trans>
                    </Form.Label>
                    <Form.Control 
                    type="tel" 
                    name="phoneNumber"
                    placeholder="Phone Number"  
                    pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}"
                    value={this.state.phoneNumber}
                    onChange={this.handleInfo}
                    required/>
                  </Form.Group>
                    </div>
                    <div className="btn-submit">
                    {this.props.user !== null ? (
                      <>
                      
                      <Form.Group controlId="exampleForm.ControlSelect1">
                      <Form.Label>
                        <Trans>tipoCambio</Trans>
                      </Form.Label>
                      <Form.Control as="select" value={this.state.tipoCambio} onChange={this.handleChange}>
                        <option value="">Select</option>
                        <option value="RD">US Dolar</option>
                        <option value="US" >RD Pesos</option>
                        {/* <option>3</option>
                        <option>4</option>
                        <option>5</option> */}
                      </Form.Control>
                    </Form.Group>

                      <p><strong>Price: </strong>
                      <span className="price">
                        <CurrencyFormat value={this.state.price} displayType={'text'} thousandSeparator={true} decimalScale={2} prefix={'$'} renderText={value => <div>{value}</div>} />
                      </span>
                      { this.state.totalTipoCambio !== 0 && 
                        <span className="price">
                        <CurrencyFormat value={this.state.totalTipoCambio} displayType={'text'} thousandSeparator={true} decimalScale={2} prefix={'$'} renderText={value => <div>{value}</div>} />
                      </span>
                      }
                      </p>
                      <button className='btn' type='button' onClick={this.handleSubmit} onChange={this.onClick}>
                        <Trans>GetTransfer</Trans>
                      </button>
                      </>
                      
                      ): <div> 
                        <Trans>
                          YouNeedTo
                        </Trans> {' '}
                        <Link to="/login">
                          <Trans>login</Trans>
                        </Link> {' '}
                        <Trans>toContinue</Trans>
                        </div>}

                        {/* <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#exampleModal">
                          Launch demo modal
                        </button> */}
                    </div>
          </div>
           {/* Modal */}
        <div className="modal fade" id="exampleModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">
                  Pedido de Traslado
                </h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <h4 className="text-center">
                  Numero de Solicitud de Traslado: <strong>7255739</strong>
                </h4>
                <p className="text-justify">
                  Su Solicitud ha sido agendada Correctamente, Le hemos enviado un Email 
                  con su Solicitud con los detalles e Informaciones del mismo.
                </p>
                <p className="text-center">
                  Le deseamos un Feliz Viaje.
                </p>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                {/* <button type="button" class="btn btn-primary">Save changes</button> */}
              </div>
            </div>
          </div>
        </div>
        </div>
      )
    }
  }

  const Directions = withTranslation()(DirectionsComponent)
  export default React.memo(Directions);
  