import React, { Component } from 'react';
//Importing style css
import '../css/buttonTransfer.css';
import {Link} from '@reach/router';
import { withTranslation, Trans } from 'react-i18next';

class ButtonTransferComponent extends Component {
    render() {
         return(
            <div>
                <button className="learn-more">
                    <span className="circle" aria-hidden="true">
                    <span className="icon arrow"></span>
                    </span>
                    <Link to="/getTransfer" className="button-text"><Trans>GetTransfer</Trans></Link>
                </button>
            </div>
        )
    }
}

const ButtonTransfer = withTranslation()(ButtonTransferComponent)
export default ButtonTransfer;