import React, { Component } from 'react';
//Importing style css
import '../css/buttonTransfer.css';
import {Link} from '@reach/router';
import { withTranslation, Trans } from 'react-i18next';

class ButtonRentComponent extends Component {
    render() {
         return(
            <div>
                <button className="learn-more">
                    <span className="circle" aria-hidden="true">
                    <span className="icon arrow"></span>
                    </span>
                    <Link to="/rent" className="button-text"><Trans>Rent</Trans></Link>
                </button>
            </div>
        )
    }
}

const ButtonRent = withTranslation()(ButtonRentComponent)
export default React.memo (ButtonRent);