import React, { Component } from 'react';
import { 
    Row,
    Col,
    Card,
    Container,
    Accordion, 
    Button 
} from 'react-bootstrap';
import '../css/driveDashboard.css';

//Importing currency Format
import CurrencyFormat from 'react-currency-format';

//Importing icons 
import {
    MdKeyboardArrowDown
} from 'react-icons/md';
import { 
    AiOutlineMobile,
    AiOutlineCarryOut
 } from 'react-icons/ai';
import { FaCar } from 'react-icons/fa';
import { MdDone } from 'react-icons/md';

//Importing i18n Translate Components
import { withTranslation, Trans } from 'react-i18next';

//Importing Firebase 
import firebase from '../key_conections/firebase';
import mapStyle from '../css/mapStyle';

//import { DistanceMatrixService } from '@react-google-maps/api';
const { GoogleMap, DirectionsService, DirectionsRenderer } = require("@react-google-maps/api");
// const ScriptLoaded = require("@react-google-maps/api").default;

const options = {
  styles: mapStyle
}

class DriverDashboardComponent extends Component {
    constructor(props){
        super(props);
        this.state = {
            response: null,
            contentDriver : [],
            keyDriver: [],
            childKey: [],
            userKey: [],
            taked: false,
            userID: null,
            totalEarn: [1],
            dData: null,
            tTaked: [0],
            total: [0]

        }

        this.getData = this.getData.bind(this);
        this.directionsCallback = this.directionsCallback.bind(this);
        this.getCoors = this.getCoors.bind(this);
        this.handleTakeService = this.handleTakeService.bind(this);
        this.handleSum = this.handleSum.bind(this);
        this.handleDoneService = this.handleDoneService.bind(this);
    }


    componentDidMount() {
        let currentComponent = this;
        this.getData();
        this.handleSum();
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(this.getCoors)
        } else {
            this.setState({ errorMessage: 'Geolocation is no supported by this browser' })
        }

        firebase.auth().onAuthStateChanged(FBUser => {
            console.log(FBUser);
            if (FBUser) {
              this.setState({
                userID: FBUser.uid
              })
            }

            firebase.database().ref('driver-Transfers/' + FBUser.uid ).on('value', function(snap) {
                console.log(snap.key)
                let k = snap.key
                firebase.database().ref('driver-Transfers/' + k ).on('value', function(s) {
                    let earned = []
                   s.forEach(function(sChild){
                       earned.push(sChild.val().price)
                   })
                   currentComponent.setState({
                       totalEarn: earned
                   })
                })
            })
        })
    }

    getData(){
        let currentComponent = this;
            firebase.database().ref('Transfers/' ).on('value', function(snapshot) {
            
                    snapshot.forEach(function(childSnapshot) {
                        // var data = childSnapshot.val();
                         var key = childSnapshot.key;
                         console.log(key)
                            firebase.database().ref('Transfers/' ).on('value', function(snap) {
                                    let allDataDriver = []
                                    let allkey = [];  
                                    let isTaked = [];
                                    let isPrice = [];       
                                    snap.forEach(function(child) {
                                        allDataDriver.push(child.val())
                                        allkey.push(child.key);  
                                        isTaked.push(child.val().taked) 
                                        isPrice.push(child.val().precio)                                     
                                    });
                                    currentComponent.setState({
                                        keyDriver: allkey,
                                        contentDriver: allDataDriver,
                                        taked: isTaked,
                                        price: isPrice
                                    })                      
                            })
                    })                
            })    

           
    }

    directionsCallback (response) {  
        if (response !== null) {
          if (response.status === 'OK') {
            this.setState(
              () => ({
                response
              }
            )
          )       
          } else {
              console.log('response: ', response)
          }
        }
      }

      getCoors(position) {
        console.log(position.coords);
        this.setState({
            lat: position.coords.latitude,
            lng: position.coords.longitude
        })
    }
 
    handleTakeService(c, p) {
        firebase.database().ref('/driver-Transfers/'+ this.state.userID + '/' + c).set({
            taked: true
        });
        firebase.database().ref('/Transfers/' + c).update({
            taked: true
        });
       // this.props.firebase.message(uid).remove();
    }

    handleSum() {
        const earn = this.state.totalEarn;
        // const te = earn.reduce((a,b) => a + b, 0)
        console.log(earn)
    }

    handleDoneService(key, p) {
        firebase.database().ref('/driver-Transfers/'+ this.state.userID + '/' + key).set({
            done: true,
            price: p
        });
        firebase.database().ref('/Transfers/' + key).update({
            done: true
        });
    }


    render() {
        
        return(
            <Container>
               <Row>
                   <Col sm={4}>
                    <h2 className="text-center">Driver Dashboard</h2>
                    <div className="tripInfo">
                      <div className="totalTrip">
                          <div className="Card-title">
                                    
                                            <h2>
                                                {this.state.totalEarn.length}
                                            </h2>                           
                             
                              <p className="text-center">Total Trip</p>
                          </div>
                      </div>
                      <div className="totalTrip">
                          <div className="Card-title">                                                              
                                 
                            <h2>US {' '} { new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format( this.state.totalEarn.reduce((a,b) => 
                             a + b, 0
                            ))  }   </h2>
                                 
                              <p className="text-center">
                                  Total Earn
                              </p>
                          </div>
                      </div>
                    </div>
                   </Col>
                   <Col sm={8}>
                       <Row>
                           <Col sm={4}></Col>
                           <Col sm={4}></Col>
                           <Col sm={4}></Col>
                       </Row>
                       <Row>
                          <Col sm={12} className="reverse">
                          {this.state.contentDriver.map((wl, i) => {
                            //   console.log(wl)
                                return ( 
                                    <Accordion key={i}>
                                        <Card className="Card-style">
                                            <div className="action">
                                                {wl.taked === true && <span className="text-taked mr-3"><AiOutlineCarryOut /></span>}
                                                { wl.cancel === true && <span className="text-danger trip tripCancel mr-3"><Trans>TripCancel</Trans></span> }
                                                { wl.done === true && <span className="text-success trip tripCancel mr-3"><Trans>Done</Trans></span>}
                                            </div>
                                        <Card.Header>
                                            <Row>
                                                <Col sm={3}>
                                                    <h6>{wl.name}</h6>
                                                    <small>
                                                        { wl.telefono }
                                                    </small> <br />
                                                    <small>
                                                        <a href={"mailto:" + wl.correo }>{ wl.correo }</a>
                                                    </small>
                                                </Col>
                                                <Col sm={6}>
                                                        <small>
                                                            <strong><Trans>Origen</Trans></strong> {wl.Partida}
                                                        </small>  <br />
                                                        <small>
                                                            <strong><Trans>CarType</Trans></strong> {wl.vehiculo}
                                                        </small>
                                                </Col>
                                                <Col sm={3} className="text-center trans-icons">
                                                    <span className="car-icon" onClick={() => this.handleTakeService(this.state.keyDriver[i])}>
                                                        <FaCar />
                                                    </span>
                                                    <span className="check" onClick={() => this.handleDoneService(this.state.keyDriver[i], wl.precio)}>
                                                        <MdDone className="done"/>
                                                    </span>
                                                    <a href={"tel:" + wl.telefono } className="a-phone">
                                                        <AiOutlineMobile className="phone-icon"/>
                                                    </a>
                                                <Accordion.Toggle as={Button} variant="link" eventKey="0" className="driver-info_icon">
                                                    <MdKeyboardArrowDown />
                                                </Accordion.Toggle>
                                                </Col>
                                            </Row>
                                        </Card.Header>
                                        <Accordion.Collapse eventKey="0">
                                            <Card.Body>
                                                <Row>
                                                    <Col sm={6}>
                                                    <GoogleMap
                                                    // required
                                                    id='direction'
                                                    // required
                                                    mapContainerStyle={{
                                                        height: '400px',
                                                        width: '100%'
                                                    }}
                                                    // required
                                                    zoom={14}
                                                    // required
                                                    center={{
                                                        lat: this.state.lat,
                                                        lng: this.state.lng
                                                    }}

                                                    options = { options }
                                                    // optional
                                                    // onClick={this.onMapClick}
                                                    // optional
                                                    onLoad={map => {
                                                        console.log('DirectionsRenderer onLoad map: ', map)
                                                    }}
                                                    // optional
                                                    onUnmount={map => {
                                                        console.log('DirectionsRenderer onUnmount map: ', map)
                                                    }}
                                                    >
                                                    {
                                                        (
                                                        wl.Destino !== '' &&
                                                        wl.Partida !== ''
                                                        ) && (
                                                        <DirectionsService
                                                            // required
                                                            options={{ 
                                                            destination: wl.Destino,
                                                            origin: wl.Partida,
                                                            travelMode: 'DRIVING'
                                                            }}
                                                            // required
                                                            callback={this.directionsCallback}
                                                            // optional
                                                            onLoad={directionsService => {
                                                            console.log('DirectionsService onLoad directionsService: ', directionsService)
                                                            }}
                                                            // optional
                                                            onUnmount={directionsService => {
                                                            console.log('DirectionsService onUnmount directionsService: ', directionsService)
                                                            }}
                                                        />
                                                        )
                                                    }
                                        
                                                    {
                                                        this.state.response !== null && (
                                                        <DirectionsRenderer
                                                            // required
                                                            options={{ 
                                                            directions: this.state.response
                                                            }}
                                                            // optional
                                                            onLoad={directionsRenderer => {
                                                            console.log('DirectionsRenderer onLoad directionsRenderer: ', directionsRenderer)
                                                            console.log(directionsRenderer.directions.routes[0].legs[0].distance)
                                                            this.setState({
                                                                distance: directionsRenderer.directions.routes[0].legs[0].distance.text,
                                                                duration: directionsRenderer.directions.routes[0].legs[0].duration.text,
                                                                distVal: directionsRenderer.directions.routes[0].legs[0].distance.value
                                                            })
                                                            }}
                                                            // optional
                                                            onUnmount={directionsRenderer => {
                                                            console.log('DirectionsRenderer onUnmount directionsRenderer: ', directionsRenderer)
                                                            }}
                                                        />
                                                        )
                                                    } 
                                                    </GoogleMap>
                                                    </Col>
                                                    <Col sm={6}>
                                                        <ul className="driver-list">
                                                            <li> 
                                                                <small>
                                                                    <strong><Trans>Destination</Trans></strong> {wl.Destino}
                                                                </small>
                                                            </li>
                                                            <li>
                                                                <small>
                                                                    <strong><Trans>Date</Trans></strong> {wl.fecha} {' '}
                                                                    <strong><Trans>Time</Trans></strong> {wl.hora}
                                                                </small>
                                                            </li>
                                                            <li> 
                                                                <small>
                                                                    <strong><Trans>Adults</Trans></strong> {wl.pasajeros}
                                                                </small>
                                                            </li>
                                                            <li> 
                                                                <small>
                                                                    <strong><Trans>Comment</Trans></strong> {wl.comentarios}
                                                                </small>
                                                            </li>
                                                            <li>
                                                                <small>
                                                                    <strong><Trans>Distance</Trans></strong> {wl.Distancia} <br />
                                                                    <strong><Trans>Price</Trans></strong>
                                                                     <CurrencyFormat value={wl.precio} displayType={'text'} thousandSeparator={true} decimalScale={2} prefix={'$'} renderText={value => <div className="price" >{value}</div>} />
                                                                </small>
                                                            </li>
                                                        </ul>
                                                    </Col>
                                                </Row>
                                            </Card.Body>
                                        </Accordion.Collapse>
                                        </Card>
                                    </Accordion>
                    )
                        })}
                          </Col> 
                       </Row>
                   </Col>
               </Row>
            </Container>
        )
    }
}
const DriverDashboard = withTranslation()(DriverDashboardComponent)
export default DriverDashboard;