import React, { Component } from 'react';
import {
    Row,
    Col
} from 'react-bootstrap';
import {Link} from '@reach/router';
import '../css/footer.css';

//Importing icons 
import { 
    TiSocialFacebook,
    TiSocialLinkedin,
    TiSocialInstagram
} from 'react-icons/ti'

//Importing i18n translate
import { withTranslation, Trans } from 'react-i18next';

class FooterComponent extends Component {

    render() {
        return(
            <div className="footerContainer">
                 <Row>
                    <Col sm>
                        <h5>
                            <Trans>
                            Services
                            </Trans>
                        </h5>
                        <ul>
                            <li>
                            <Link to="/">
                                <Trans>
                                    AirportTransfers
                                </Trans>
                            </Link>
                            </li>
                            <li>
                            <Link to="/">
                                <Trans>
                                    VIPTransfer
                                </Trans>
                            </Link>
                            </li>
                            {/* <li>
                            <Link to="/">Airport Transfer</Link>
                            </li> */}
                        </ul>                        
                    </Col>
                    <Col sm>
                        <h5>
                            <Trans>
                            WebsiteMap
                            </Trans>
                        </h5>
                        <ul>
                            <li>
                                <Link to="/">
                                    <Trans>
                                    AboutUs
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/registerAsCarrier">
                                    <Trans>
                                    BecomeCarrier
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/">
                                    <Trans>
                                    CheckIn
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/">
                                    <Trans>
                                    FrequentQuestions
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/">
                                    <Trans>
                                    ForBusiness
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/">
                                    <Trans>
                                        Login
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/">
                                    <Trans>
                                    OURFLEET
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/">
                                    <Trans>
                                    PrivacyPolicies
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/">
                                    <Trans>
                                    ReturnPolicies
                                    </Trans>
                                </Link>
                            </li>
                            <li>
                                <Link to="/">
                                    <Trans>
                                    TermofUse
                                    </Trans>
                                </Link>
                            </li>
                        </ul>
                    </Col>
                    <Col sm>
                        <h5>
                            <Trans>
                            Contact
                            </Trans>
                        </h5>
                    </Col>
                    <Col sm>
                        <h5>
                            <Trans>
                            Social
                            </Trans>
                        </h5>
                        <ul>
                            <li className="social">
                                <TiSocialFacebook />
                                <span>Facebook</span>
                            </li>
                            <li className="social">
                                <TiSocialLinkedin />
                                <span>Linkedin</span>
                            </li>
                            <li className="social">
                                <TiSocialInstagram />
                                <span>Instagram</span>
                            </li>
                        </ul>
                    </Col>
                </Row>
            </div>
        )
    }
}

const Footer = withTranslation()(FooterComponent)
export default Footer;