import React, { Component } from 'react';
import Home from './components/home';
import './App.css';

//firebase 
import firebase from 'firebase';

// Router
import {Router, navigate} from '@reach/router';

// Components
import NavigationBar from './components/navbar';
import Login from './components/login';
import Register from "./components/register";
import Profile from './components/profile';
import Footer from './components/footer';
import Welcome from './components/welcome';
import Directions from './components/transferForm';
import RegisterAsCarrier from './components/regCarrier';
import Admin from './components/admin';
import DriverDashboard from './components/driver-Dashboard';
import Rent from './components/rentForm';
import PassRecovery from './components/PasswordRecovery';

//Testing Component Delete later
//import FormTest from './components/formTest';

//Importing i18n to be Bunddle
import './i18n';

class App extends Component {

  constructor() {
    super();
    this.state = {
      user: null,
      displayName: null,
      mail: null,
      userID: null,
      created: null,
      lastLogin: null,
      userRole: null,
      xChange: null
    };
  }
  
  componentDidMount() {
    // Getting user data from Firebase
   firebase.auth().onAuthStateChanged(FBUser => {
     if (FBUser) {
       console.log(FBUser)
       this.setState({
         user: FBUser,
         displayName: FBUser.displayName,
         userID: FBUser.uid,
         created: FBUser.metadata.creationTime,
         lastLogin: FBUser.metadata.lastSignInTime,
         userImg: FBUser.metadata.photoURL,
         mail: FBUser.email
        // userRole: ''
       })       
     }
   });
   
   //let endpoint = 'latest'
   let access_key = 'e5b2ad2857024a1084fa10af204de663' //'fd2f23446a19844fb3f951785bb3e4e9'
   //let ExchangeType = 'USD'
   
   //const ex = 'http://data.fixer.io/api/' + endpoint + '?access_key=' + access_key + '&symbols=' + ExchangeType
   const ex = "https://openexchangerates.org/api/latest.json?app_id=" + access_key;


   fetch(ex, {
       // "mode": 'no-cors',
       "method": "GET",
       // "headers": {
       //     "x-rapidapi-host": "currency13.p.rapidapi.com",
       //     "x-rapidapi-key": "f43c62c95dmsh880c62a47e27d56p1df237jsn4a8636802c68"
       // }
   })
   .then(response => {
       const Exchange = response.json();
       return Exchange
   }).then(Exchange => {
       console.log(Exchange.rates)
       this.setState({
           xChange: (Exchange.rates.DOP - 1)
       })
   })
   .catch(err => {
       console.log(err);
   });
  }

  registerUser = userName => {
    firebase.auth().onAuthStateChanged( FBUser => {
      FBUser.updateProfile({
        displayName: userName 
      }).then(() => {
        this.setState({
          user: FBUser,
          displayName: FBUser.displayName,
          userID: FBUser.uid
        });
        firebase.database().ref('userInfo/' + FBUser.uid).set({
          name: FBUser.displayName,
          email: FBUser.email,
          isdriver: "false",
          isAdmin: "false"
      });
        navigate('/profile');
      })
    })
  }

  logoutUser = e => {
    e.preventDefault();
    this.setState({
      displayName: null,
      userID: null,
      user: null,
      userRole: null
    });
    firebase.auth().signOut().then(() => {
      navigate('/login');
    }).catch(function(error){
      console.log(error)
    }); 
  }


  render () {

    return (
      <div className="App">
        <NavigationBar 
        user={this.state.user} 
        logoutUser={this.logoutUser}
        userID = {this.state.userID }
        />
        <header className="App-header">
          { this.state.user && <Welcome userName={this.state.displayName} logoutUser={this.logoutUser} /> }
          <Router>
            <Home path="/" user={this.state.user}/>
            <Login path="/login" />
            <Register path="/register" registerUser={this.registerUser}/>
            <Profile path="/profile" 
              created={this.state.created} 
              lastLogin={this.state.lastLogin}
              photo={this.state.userImg}
              uID={this.state.userID}
              xChange={this.state.xChange}
              mail={this.state.mail}
              name={this.state.displayName}
            />
          
            <Directions path="/getTransfer" user={this.state.user} userID={this.state.userID} xChange={this.state.xChange}/>
            <RegisterAsCarrier path="/registerAsCarrier" userID={this.state.userID}/>

            {/* <FormTest path="/form"/> */}
            <Admin path="/admin" name={this.state.displayName} xChange={this.state.xChange}/>
            <DriverDashboard path="/DriverDashboard" />
            <Rent path="/rent" userID={this.state.userID}/>
            <PassRecovery path="/RecoveryPassword"/>           
          </Router>
        </header>
        <Footer />
      </div>
    );
  }
}

export default App;
