import React, { Component } from 'react';
//Key Conection to Firebase
import firebase from '../key_conections/firebase';
import FormError from '../components/formError';
import {Trans, withTranslation } from 'react-i18next';

//importing styles and bootstrap Components
import {
    Form,
    Button
} from 'react-bootstrap';
import '../css/register.css';

import { navigate } from '@reach/router';

class RegisterComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            displayName: '',
            email: '',
            password: '',
            secondPassword: '',
            errorMessage: ''
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(e) {
        //Getting form values and set it into state to pass
        //Getting Error Message
        const itemName = e.target.name;
        const itemValue = e.target.value;

        this.setState({[itemName]: itemValue}, () => {
            if(this.state.password !== this.state.secondPassword) {
                this.setState({errorMessage: 'Password does\'t match'})
            } else {
                this.setState({errorMessage: null})
            }
        });
    }

    handleSubmit(e) {
        //Save user information to firebase database
        let registrationInfo = {
            displayName: this.state.displayName,
            email: this.state.email,
            password: this.state.password
        }
        e.preventDefault();

        firebase
        .auth()
        .createUserWithEmailAndPassword(
            registrationInfo.email,
            registrationInfo.password
        ).then(() => {
            this.props.registerUser(registrationInfo.displayName)
            setTimeout({
                function() {
                    navigate('/profile')
                }
            }, 5000)
        })
        .catch(error => {
            if(error.message !== null) {
                this.setState({errorMessage: error.message})
            } else {
                this.setState({errorMessage: null})
            }
        })
    }

    render() {
        return(
            <div className="container">
                <h1 className="text-center mt-4"><Trans>Register</Trans></h1>
            <Form onSubmit={this.handleSubmit} className="form"> 
                {this.state.errorMessage !== null ? (
                <FormError Message={this.state.errorMessage}/>
                 ): null}
                <Form.Group>
                <Form.Label><Trans>Name</Trans></Form.Label>
                <Form.Control 
                    type="text" 
                    placeholder="Enter Name"
                    className=""
                    id="displayName"
                    name="displayName"
                    value={this.state.displayName}
                    onChange={this.handleChange}
                /></Form.Group>
                    <Form.Group>
                        <Form.Label><Trans>Email</Trans></Form.Label>
                        <Form.Control 
                            type="email" 
                            placeholder="Enter email"
                            className=""
                            id="email"
                            name="email"
                            value={this.state.email}
                            onChange={this.handleChange}
                        />
                        <Form.Text className="text-muted">
                        <Trans>NeverShared</Trans>
                        </Form.Text>
                    </Form.Group>

                    <Form.Group>
                        <Form.Label><Trans>Password</Trans></Form.Label>
                        <Form.Control 
                            type="password" 
                            placeholder="Password"
                            className=""
                            id="password"
                            name="password"
                            value={this.state.password}
                            onChange={this.handleChange}
                            required
                        />
                    </Form.Group>
                <Form.Group>
                    <Form.Label><Trans>ConfirmPassword</Trans></Form.Label>
                    <Form.Control 
                        type="password" 
                        placeholder="Enter Password"
                        className=""
                        id="secondPassword"
                        name="secondPassword"
                        value={this.state.secondPassword}
                        onChange={this.handleChange}
                        required
                    />
                </Form.Group>
                <div className="text-center">
                    <Button variant="primary" type="submit">
                        <Trans>REGISTER</Trans>
                    </Button>
                </div>
</Form>
</div>
        )
    }
}
const Register = withTranslation()(RegisterComponent)
export default React.memo(Register);