import React, { Component } from 'react';
import { Container } from 'react-bootstrap';
import { withTranslation, Trans } from 'react-i18next';

import firebase from '../key_conections/firebase';
import UserList from './userList';
import RentList from './rentList';

import '../css/admin.css';

import {
    Row,
    Col,
    Card,
    // Table,
    Form
} from 'react-bootstrap';


class AdminComponent extends Component {
    constructor() {
        super()
        this.state = {
           content: [],
           weather: null,
           icon: null,
           weatherNextDay: null,
           xChange: null,
           uPlace: '',
           nPrice: 0,
           price: 0,
           terrenas: 0,
           ala: 0,
           juanDolio: 0,
           puertoPlata:  0,
           puntaCana: 0,
           soCa: 0,
           terPuntaCana: 0,
           bayahibe: 0,
           samana: 0,
           Renta: [],
           admin: [],
           driver: []  
        
        }
        this.getData = this.getData.bind(this);
        this.handlePlaces = this.handlePlaces.bind(this);
        this.handlePrice = this.handlePrice.bind(this);
        this.handlePriceInfo = this.handlePriceInfo.bind(this);
    }

    componentDidMount() {
         fetch("https://weatherbit-v1-mashape.p.rapidapi.com/forecast/3hourly?lang=en&lat=18.4644239999998&lon=-69.945388499999", {
            "method": "GET",
            "headers": {
                "x-rapidapi-host": "weatherbit-v1-mashape.p.rapidapi.com",
                "x-rapidapi-key": "f43c62c95dmsh880c62a47e27d56p1df237jsn4a8636802c68"
            }
        })
        .then(response => {
            const data = response.json();
            return data
        }).then(data => {
            this.setState({
                weather: data.data[0].temp,
                icon: data.data[0].weather.icon,
                weatherNextDay: data.data[5].temp
            })
        })
        .catch(err => {
            console.log(err);
        });

        this.getData()
        this.handlePrice()
    }

    getData(){
        let currentComponent = this;
            firebase.database().ref('userInfo/').on('value', function(snapshot) {
            
                    snapshot.forEach(function(childSnapshot) {
                         var data = childSnapshot.val();
                         console.log(data)
                         //  var key = childSnapshot.key;
                            
                            firebase.database().ref('userInfo/').on('value', function(snap) {
                                    let allData = [];  
                                    let isAdmin =[];
                                    let isDriver =[];        
                                    snap.forEach(function(child) {
                                        allData.push(child.val());
                                        isAdmin.push(child.val().isAdmin);
                                        isDriver.push(child.val().isdriver);
                                    });
                                    currentComponent.setState({
                                        content: allData,
                                        admin: isAdmin,
                                        diver: isDriver
                                    })                                
                            })
                    })                
            })

            // getting Price data from database
            firebase.database().ref('price/').on("value", function(snapPrice) {
                // let terrenas = []
                // terrenas.push(snapPrice.val())
                currentComponent.setState({
                    terrenas: snapPrice.val().Terrenas,
                    ala: snapPrice.val().AeropuertoLasAmericas,
                    juanDolio: snapPrice.val().JuanDolio,
                    puertoPlata: snapPrice.val().PuertoPlata,
                    puntaCana: snapPrice.val().PuntaCana,
                    soCa: snapPrice.val().SosuaCabarete,
                    terPuntaCana: snapPrice.val().TerrenasPuntaCana,
                    bayahibe: snapPrice.val().bayahibe,
                    samana: snapPrice.val().samana
                })
            }) 
            firebase.database().ref('Rent/').on('value', function(rent) {
                let rentId = []
                rent.forEach(function(snapRent){
                    rentId.push(snapRent.val())
                })
                currentComponent.setState({
                    Renta: rentId
                })
            })
    }

    // handle places and price 
    handlePlaces(e) {
        e.preventDefault();
        this.setState({
            uPlace: e.target.value
        })

        setTimeout(() => {
            const p = this.state.uPlace
            if( p === 'Aeropuerto Las Amricas') {
                this.setState({
                    price: this.state.ala 
                })
            } else if( p === "terrenas") {
                this.setState({
                    price: this.state.terrenas
                })
            } else if( p === "juanDolio") {
                this.setState({
                    price: this.state.juanDolio
                })
            } else if( p === "puertoPlata") {
                this.setState({
                    price: this.state.puertoPlata
                })
            } else if( p === "puntaCana") {
                this.setState({
                    price: this.state.puntaCana
                })
            } else if( p === "soCa") {
                this.setState({
                    price: this.state.soCa
                })
            } else if( p === "terPuntaCana") {
                this.setState({
                    price: this.state.terPuntaCana
                })
            } else if( p === "bayahibe") {
                this.setState({
                    price: this.state.bayahibe
                })
            } else if( p === "samana") {
                this.setState({
                    price: this.state.samana
                })
            }
        }, 1500)
    }

    handlePriceInfo(e) {
        const itemValue = e.target.value;
        console.log(itemValue)
        this.setState({nPrice: itemValue});
    }

    handlePrice(e) {
      // e.preventDefault()
      const p = this.state.uPlace;
      
        if(p === "Aeropuerto Las Amricas") {
            firebase.database().ref('price/').update({
                AeropuertoLasAmericas: this.state.nPrice
            })
           e.preventDefault();
        } else if( p === "juanDolio") {
            firebase.database().ref('price/').update({
               JuanDolio : this.state.nPrice
            })
        } else if( p === "puertoPlata") {
            firebase.database().ref('price/').update({
                PuertoPlata: this.state.nPrice
            })
        } else if( p === "puntaCana") {
            firebase.database().ref('price/').update({
               PuntaCana : this.state.nPrice
            })
        } else if( p === "soCa") {
            firebase.database().ref('price/').update({
               SoCa : this.state.nPrice
            })
        } else if( p === "terrenas") {
            firebase.database().ref('price/').update({
               Terrenas : this.state.nPrice
            })
        } else if( p === "terPuntaCana") {
            firebase.database().ref('price/').update({
               TerrenasPuntaCana : this.state.nPrice
            })
        } else if( p === "bayahibe") {
            firebase.database().ref('price/').update({
                bayahibe: this.state.nPrice
            })
        } else if( p === "samana") {
            firebase.database().ref('price/').update({
                samana: this.state.nPrice
            })
        }
    }

    render() {
        const { name, xChange } = this.props
        const url = `https://www.weatherbit.io/static/img/icons/${this.state.icon}.png`
        return(
            <Container>
                <h3 className="text-center"><Trans>WelcomeAdmin</Trans> {name}</h3>
                <Row>
                    <Col sm={4}>
                        <Row>
                            <Col sm={6}>
                            <Card style={{ margin: 'auto' }}>
                            <Card.Body className="text-center">
                                <Card.Text className="visit">
                                    US$ {new Intl.NumberFormat("en-US", {maximumSignificantDigits: 5}).format(xChange)}<br />
                                    <span><Trans>Exchange</Trans></span>
                                </Card.Text>
                            </Card.Body>
                            </Card>
                            </Col>
                            <Col sm={6}>
                            <Card style={{ margin: 'auto' }}>
                            <Card.Body className="text-center">
                                <Card.Text className="visit weather">
                                    {this.state.weather}&deg; <br />
                                    <img src={url} className="icon-img" alt="Weather icons"/> <br />
                                    <span className="nextDay"><Trans>Tomorrow</Trans>{' '}{this.state.weatherNextDay}</span>
                                </Card.Text>
                            </Card.Body>
                            </Card>
                            </Col>
                        </Row>
                    </Col>
                    <Col sm={8}>
                    <form onSubmit={this.handlePrice}>
                        <Form.Group>
                            <Form.Label><Trans>ServicePrice</Trans></Form.Label>
                            <Form.Control 
                                type="number" 
                                placeholder="Enter a new Price"
                                className=""
                                id="nPrice"
                                name="nPrice"
                                value={this.state.nPrice}
                                onChange={this.handlePriceInfo}
                            />
                        </Form.Group>
                        <div className="form-group">
                             <label htmlFor="exampleFormControlSelect1"><Trans>TransPrice</Trans> RD${' '} {this.state.price}</label>
                            <select className="form-control" id="exampleFormControlSelect1" onChange={this.handlePlaces} >
                                <option value="null">Seleccione</option>
                                <option value="Aeropuerto Las Amricas">Santo Domingo - Aeropuerto Las Amricas</option>
                                <option value="bayahibe">Santo Domingo - Bayahibe</option>
                                <option value="juanDolio">Santo Domingo - Juan Dolio</option>
                                <option value="puertoPlata">Santo Domingo - Puerto Plata</option>
                                <option value="puntaCana">Santo Domingo - Punta Cana</option>
                                <option value="samana">Santo Domingo - Samana</option>
                                <option value="soCa">Santo Domingo - Sosua-Cabarete</option>
                                <option value="terrenas">Santo Domingo - Terrenas</option>
                                <option value="terPuntaCana">Terrenas - Punta Cana</option>
                            </select>
                        </div>
                        <input type="submit" value="Editar" className="btn btn--cancel_transfer" />
                    </form>
                    </Col>
                </Row>
                <Row>
                <Col sm={6}>
                    <h3 className="text-center">Lista de Usuarios</h3>
                        <UserList dataUser={this.state.content}/>
                </Col>
                    <Col sm={6}>
                        <h3 className="text-center">
                            <Trans>Rent</Trans>
                        </h3>
                        <RentList rent={this.state.Renta} />
                    </Col>
                </Row>
            </Container>
        )
    }
}

const Admin = withTranslation()(AdminComponent)
export default Admin;