import React, { Component } from 'react';
//Key Conection to Firebase
import firebase from '../key_conections/firebase';
import FormError from '../components/formError';
//Importing Link from @reach-router
import {Link} from '@reach/router';

//importing styles and bootstrap Components
import {
    Form,
    Button
} from 'react-bootstrap';
import '../css/register.css';

class RegisterAsCarrier extends Component {
    constructor(props) {
        super(props);
        this.state = {
            displayName: '',
            email: '',
            password: '',
            secondPassword: '',
            errorMessage: '',
            phoneNumber: '',
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleChange(e) {
        //Getting form values and set it into state to pass
        //Getting Error Message
        const itemName = e.target.name;
        const itemValue = e.target.value;

        this.setState({[itemName]: itemValue}, () => {
            if(this.state.password !== this.state.secondPassword) {
                this.setState({errorMessage: 'Password does\'t match'})
            } else {
                this.setState({errorMessage: null})
            }
        });
    }

    handleSubmit(e) {
        if(!this.props.userID === null) {
        //Save user information to firebase database
        let registrationInfo = {
            displayName: this.state.displayName,
            email: this.state.email,
            password: this.state.password,
            phoneNumber: this.state.phoneNumber
        }
        e.preventDefault();

        firebase
        .auth()
        .createUserWithEmailAndPassword(
            registrationInfo.email,
            registrationInfo.password
        ).then(() => {
            this.props.registerUser(registrationInfo.displayName)
        }).then(() => {
            // saving user data in firebase
            firebase.database().ref('userInfo/').push({
                name: this.state.displayName,
                email: this.state.email,
                isdriver: "true",
                isAdmin: "false"
            })
        })
        .catch(error => {
            if(error.message !== null) {
                this.setState({errorMessage: error.message})
            } else {
                this.setState({errorMessage: null})
            }
        })
    } else {
        // Saving user data in firebase
        e.preventDefault();
        firebase.database().ref('userInfo/' + this.props.userID).set({
                name: this.state.displayName,
                email: this.state.email,
                isdriver: "true",
                isAdmin: "false"
        })
    }
    }

    render() {
        return(
            <div className="container">
                <h1 className="text-center mt-4"> Register as Carrier </h1>
            <Form onSubmit={this.handleSubmit} className="form"> 
                {this.state.errorMessage !== null ? (
                <FormError Message={this.state.errorMessage}/>
                 ): null}
                <Form.Group>
                <Form.Label>Name</Form.Label>
                <Form.Control 
                    type="text" 
                    placeholder="Enter Name"
                    className=""
                    id="displayName"
                    name="displayName"
                    value={this.state.displayName}
                    onChange={this.handleChange}
                /></Form.Group>
                    <Form.Group>
                        <Form.Label>Email address</Form.Label>
                        <Form.Control 
                            type="email" 
                            placeholder="Enter email"
                            className=""
                            id="email"
                            name="email"
                            value={this.state.email}
                            onChange={this.handleChange}
                        />
                        <Form.Text className="text-muted">
                        We'll never share your email with anyone else.
                        </Form.Text>
                    </Form.Group>
                    <Form.Group>
                        <Form.Label>Phone Number</Form.Label>
                        <Form.Control 
                            type="number" 
                            placeholder="Enter Phone Number"
                            className=""
                            id="phoneNumber"
                            name="phoneNumber"
                            value={this.state.phoneNumber}
                            onChange={this.handleChange}
                        />
                        <Form.Text className="text-muted">
                        We'll never share your email with anyone else.
                        </Form.Text>
                    </Form.Group>
                    {!this.props.userID && 
                    <div>
                    <Form.Group>
                        <Form.Label>Password</Form.Label>
                        <Form.Control 
                            type="password" 
                            placeholder="Password"
                            className=""
                            id="password"
                            name="password"
                            value={this.state.password}
                            onChange={this.handleChange}
                            required
                        />
                    </Form.Group>
    
                    <Form.Group>
                        
                        <Form.Label>Confirm Password</Form.Label>
                        <Form.Control 
                            type="password" 
                            placeholder="Enter Password"
                            className=""
                            id="secondPassword"
                            name="secondPassword"
                            value={this.state.secondPassword}
                            onChange={this.handleChange}
                            required
                        />
                    </Form.Group>
                    
                    </div> }
                <Form.Group controlId="formBasicCheckbox">
                <span> 
                    <Form.Check type="checkbox" label="I have read and accept the " />
                    <Link to="/" className="link">TERMS OF USE</Link> 
                </span>
                </Form.Group>
                <div className="text-center">
                    <Button variant="primary" type="submit">
                        Register
                    </Button>
                </div>
                </Form>
                </div>
        )
    }
}


export default RegisterAsCarrier