import React, { Component } from 'react';
import { Form, Container } from 'react-bootstrap';
import {Trans, withTranslation} from 'react-i18next';
import firebase from 'firebase';
import '../css/passwordRecovery.css';

class PassRecoveryComponent extends Component {
    constructor() {
        super()
        this.state = {
            mail: ''
        }
        this.handleForgotPass = this.handleForgotPass.bind();
        this.handleChange = this.handleChange.bind(this);
    }

    handleForgotPass(e) {
        const recovery =  firebase.auth();
        const mail = this.state.email;

        if(mail !== '') {
            recovery.sendPasswordResetEmail(mail).then(function() {
                window.alert('Email was sent to you.')
            }).catch(error => {
                console.log(error);
            })
        }
    }

    handleChange(e) {
        //Getting form values and set it into state to pass
        //Getting Error Message
        const itemName = e.target.name;
        const itemValue = e.target.value;

        this.setState({[itemName]: itemValue});
    }

    render() {
        return(
            <Container>
                <h1 className="text-center"><Trans>RecoveryPassword</Trans></h1>
                <Form onClick={this.handleForgotPass} className="form-recovery">
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label><Trans>Email</Trans></Form.Label>
                        <Form.Control 
                        type="email" 
                        placeholder="Enter email"
                        name="email"
                        value={this.state.mail} 
                        onChange={this.handleChange}
                        required/>
                    </Form.Group>
                    <button className='btn btn-recovery' type='button' onClick={this.handleForgotPass}>
                        <Trans>Recovery</Trans>
                    </button>
                </Form>
            </Container>
        )
    }
}
const PassRecovery = withTranslation()(PassRecoveryComponent)
export default React.memo(PassRecovery);