import React, { Component } from 'react';
// css
import '../css/navbar.css';
import {Link} from '@reach/router';
import {
    Nav,
    Navbar,
    Form,
    Button,
    NavDropdown
} from 'react-bootstrap';
// Importing giLion icons 
import { GiLion } from 'react-icons/gi';

//Importing i18n Components 
import { withTranslation, Trans } from 'react-i18next';
import '../i18n';
import firebase from '../key_conections/firebase';

class Navigation extends Component {
    constructor(props){
        super(props);
        this.state = ({
            admin: false,
            driver: false,
            email: null,
            name: ''
        })
    }

    componentDidMount() {
        firebase.auth().onAuthStateChanged(FBUser => {
            console.log(FBUser);
            if (FBUser) {
              this.setState({
                user: FBUser,
                displayName: FBUser.displayName,
                userID: FBUser.uid,
                created: FBUser.metadata.creationTime,
                lastLogin: FBUser.metadata.lastSignInTime,
                userImg: FBUser.metadata.photoURL
               // userRole: ''
              })
              
            }
            

            // Falta esta seccion sacar informacion a pantalla 
            let currentComponent = this
        firebase.database().ref('userInfo/' + FBUser.uid ).on('value', function(snap) {
            //var username = snap.val()
            var nk = snap.key
            console.log(nk)
            firebase.database().ref('userInfo/' + nk ).on('value', function(s) {
                let allData = []; 
                let allkey = []
                console.log(s.val())
                const x = s.val();
                console.log(x)
               // allData.push(snap.val());       
                snap.forEach(function(child) {
                  allkey.push(child.key);
                  allData.push(child.val());
                });
                currentComponent.setState({
                    admin: x.isAdmin,
                    driver: x.isdriver,
                    email: x.email,
                    name: x.name
                })                                
         })
            // ...
          });
          }); 
    }
  
    render() {
        const {user, logoutUser } = this.props;
        // Translate const
        const { i18n } = this.props;
        const changeLanguage = lng => {
            i18n.changeLanguage(lng);
        };

        return(
            <Navbar expand="lg">
            <Navbar.Brand href="#home">
              <Link to="/"> 
                <GiLion />
              </Link>
            </Navbar.Brand>
            <Navbar.Text className="user-icon" href="#"></Navbar.Text>
            <NavDropdown title="Translate" aira-controls="translate-nav">
                <NavDropdown.Item onClick={() => changeLanguage('es')}>ES</NavDropdown.Item>
                <NavDropdown.Item onClick={() => changeLanguage('en')}>EN</NavDropdown.Item>
            </NavDropdown>
           
            <Navbar.Toggle className='toggle' aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav" className="text-center">
                <Nav className="mr-auto">
                <Link className="nav-link" to="/">
                    <Trans>Texto1</Trans>
                </Link>
                <Nav.Link href="#link"><Trans>Villas</Trans></Nav.Link>
                <Nav.Link href="#link"><Trans>JetskiRent</Trans></Nav.Link>
                { user && (
                    <Nav>
                        <Link to="/profile" className="nav-link">
                            <Trans>
                            Profile
                            </Trans>
                        </Link>
                    </Nav>
                    )}
                    { this.state.admin === "true" && (
                        <Nav>
                        <Link to="/admin" className="nav-link">
                            <Trans>
                            AdminDashboard
                            </Trans>
                        </Link>
                        </Nav>
                    )}
                     { this.state.driver === "true" && (
                         <Nav>
                        <Link to="/DriverDashboard" className="nav-link">
                            <Trans>
                            DriverDashboard
                            </Trans>
                        </Link>
                        </Nav>
                    )}
                <NavDropdown title="menu" id="basic-nav-dropdown ">
                    <NavDropdown.Item href="#action/3.1">
                        <Trans>
                        NuestrasVentajas
                        </Trans>
                    </NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.2">Nuestras Flotas</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.3">Nuestros Destinos</NavDropdown.Item>
                    <NavDropdown.Item>
                        <Link to="/registerAsCarrier">
                            <Trans>
                            ConvertirseEnTransportista
                            </Trans>
                        </Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.2">Para Negocios</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.2">Resenas</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.2">Preguntas Frecuentes</NavDropdown.Item>
                    <NavDropdown.Divider />
                </NavDropdown>
                </Nav>
                <Form className="center">
                    {user == null && (
                        <span className="center">
                            <Link className="btn btn-login" variant="outline-primary" to="/login">
                                <Trans>
                                LOGIN
                                </Trans>
                            </Link>
                            <Link className="btn btn-register" variant="outline-primary" to="/register">
                                <Trans>
                                REGISTER
                                </Trans>
                            </Link>
                        </span>
                    )}
                    {user && (
                            <Button className="btn btn-login" variant="outline-primary"
                            onClick={e => logoutUser(e)}>
                                <Trans>
                                Logout
                                </Trans>
                            </Button>
                    )}
                
                </Form>
            </Navbar.Collapse>
        </Navbar>
        )
    }
}

const NavigationBar = withTranslation()(Navigation)
export default NavigationBar;