import React, { Component } from 'react';
import { withTranslation, Trans } from 'react-i18next';
import {
    Form,
    Container,
    Row,
    Col
} from 'react-bootstrap';
// import { useForm } from "react-hook-form";

import { 
    FiUser
 } from 'react-icons/fi';
import {
    FaLuggageCart
 } from 'react-icons/fa';

import JetSki from '../images/rent-img/jetski.jpg';
import Car from '../images/Fleets/Honda-Accord-Sport-1.5T-2020.jpg';
import '../css/rentForm.css';

import firebase from 'firebase';
import {Link} from '@reach/router';



class RentComponent extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rent: '',
            rentCar: '',
            days: '',
            name: '',
            email: '',
            phone: '',
            comment: '',
            ConfirmNum: '',
            licence: '',
            StatusMail: ''
        }
        this.handleOptions = this.handleOptions.bind(this);
        this.handleRentCar = this.handleRentCar.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleInfo = this.handleInfo.bind(this);
        this.handleDays = this.handleDays.bind(this)
    }

    handleOptions(e) {
        const rentType = e.target.value;
        this.setState({
            rent: e.target.value
        })
        console.log(rentType);
    }
    handleDays(e) {
        this.setState({
            days: e.target.value
        })
    }

    handleRentCar(e) {
        this.setState({
            rentCar: e.target.value
        })
    }

    handleInfo(e) {
        const itemName = e.target.name;
        const itemValue = e.target.value;

        this.setState({[itemName]: itemValue})
      }

    handleSubmit(e) {
        e.preventDefault();
        const { rent,
            days,
            name,
            email,
            phone,
            comment
        } = this.state

         //send to a email
         let xhr = new XMLHttpRequest();
        
         xhr.addEventListener('load', () => {
             console.log(xhr.responseText)
             this.setState({
                 StatusMail: xhr.responseText
             })
         });
 
         xhr.open('GET', 'https://www.lktransfers.com/api/sendEmail/index.php?sendto=' + email +
         '&name=' + name + '&message=' + comment + '&phone=' + phone + '&rent=' + rent + '&days=' + days);
 
         xhr.send()

        let userTrans = {
            rent: this.state.rent,
            rentCar: this.state.rentCar,
            days: this.state.days,
            name: this.state.name,
            email: this.state.email,
            phone: this.state.phone,
            comment: this.state.comment
          }

        let newRent = firebase.database().ref().child('Transfers').push().key;

        var updates = {};
        updates['/Rent/' + newRent] = userTrans;
        updates['/user-Rent/' + this.props.userID + '/' + newRent] = userTrans;
      
        return firebase.database().ref().update(updates)
    }

    // const eMail = ({}) => ();

    render() {
        // const {register} = useForm();
        return (
            <main>
                <h2 className="text-center"><Trans>RentForm</Trans></h2>
                <Container>
                    {this.state.StatusMail ? this.state.StatusMail : null}
                <Form>
                <Form.Group controlId="exampleForm.ControlSelect1">
                    <Form.Label><Trans>WhatRent</Trans></Form.Label>
                    <Form.Control as="select" value={this.state.rent} onChange={this.handleOptions}>
                        <option value="">Select</option>
                        <option value="Jet ski">Jet Ski</option>
                        <option value="car">Cars</option>
                        <option value="Vacational Villa">Vacationals Villas</option>
                        <option value="Night Tours">Night Tours</option>
                    {/* <option>5</option> */}
                    </Form.Control>
                </Form.Group>
                { this.state.rent === 'Jet ski' && 
                 <div className="text-center">
                    <img src={JetSki} alt="Jet Ski images" className="rentImg"/>     
                </div>}
                {this.state.rent === 'car' && <div className="text-center">
                        <img src={Car} alt="honda car white" className="rentImg"/> 
                </div>}

                {this.state.rent === 'car' && 
                <div className="">
                 <Form.Group controlId="Phone">
                    <Form.Label><Trans>ManyDays</Trans></Form.Label>
                    <Form.Control as="select" value={this.state.days} onChange={this.handleDays}>
                        <option value="">Select</option>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                        <option value="5">5</option>
                        <option value="6">6</option>
                        <option value="7">7</option>
                        <option value="8">8</option>
                        <option value="9">9</option>
                        <option value="10">10</option>
                    {/* <option>5</option> */}
                    </Form.Control>
                </Form.Group>
                <Form.Group controlId="Phone">
                    <Form.Label><Trans>DriverLicence</Trans></Form.Label>
                    <Form.Control 
                    type="number" 
                    placeholder="Driver Licence" 
                    name="licence"
                    value={this.state.licence}
                    onChange={this.handleInfo} 
                    required/>
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlSelect1">
                    <Form.Label><Trans>CarType</Trans></Form.Label>
                    <Form.Control as="select" value={this.state.rentCar} onChange={this.handleRentCar}>
                        <option value="">Select</option>
                        <option value="economico">Economy</option>
                        <option value="compact">Compact</option>
                        <option value="midsize">Midsize</option>
                        <option value="full size">Full Side</option>
                        <option value="standard">Standard</option>
                        <option value="premium">Premium</option>
                        <option value="luxury">Luxury</option>
                        <option value="van">Van</option>
                        <option value="sport utility">Sport Utility</option>
                        <option value="convertible">Convertible</option>
                    {/* <option>5</option> */}
                    </Form.Control>
                    {this.state.rentCar === 'economico' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}4</p></Col>
                            <Col><p><FaLuggageCart />{' '}3</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'compact' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}4</p></Col>
                            <Col><p><FaLuggageCart />{' '}3</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'midsize' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}5</p></Col>
                            <Col><p><FaLuggageCart />{' '}3</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'full size' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}5</p></Col>
                            <Col><p><FaLuggageCart />{' '}3</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'standard' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}5</p></Col>
                            <Col><p><FaLuggageCart />{' '}4</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'premium' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}5</p></Col>
                            <Col><p><FaLuggageCart />{' '}3</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'luxury' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}5</p></Col>
                            <Col><p><FaLuggageCart />{' '}4</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'van' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}7</p></Col>
                            <Col><p><FaLuggageCart />{' '}4</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'sport utility' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}5</p></Col>
                            <Col><p><FaLuggageCart />{' '}5</p></Col>
                        </Row>
                    </div>}
                    {this.state.rentCar === 'convertible' && 
                    <div className="text-center text-info">
                        <Row className="CarTypeInfo">
                            <Col><p><FiUser />{' '}4</p></Col>
                            <Col><p><FaLuggageCart />{' '}2</p></Col>
                        </Row>
                    </div>}
                </Form.Group>
                </div>
                }
              
                <Form.Group controlId="Name">
                    <Form.Label><Trans>Name</Trans></Form.Label>
                    <Form.Control  
                    placeholder="juan Perez"
                    type="name" 
                    name="name"
                    value={this.state.name}
                    onChange={this.handleInfo} 
                    required/>
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label><Trans>EmailAddress</Trans></Form.Label>
                    <Form.Control 
                    type="email" 
                    placeholder="name@example.com"
                    name="email"
                    value={this.state.email}
                    onChange={this.handleInfo}
                    // register={register({ pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i })}
                    required/>
                </Form.Group>
                <Form.Group controlId="Phone">
                    <Form.Label><Trans>PhoneNumber</Trans></Form.Label>
                    <Form.Control 
                    type="number" 
                    placeholder="(809) 555 6677"
                    name="phone"
                    value={this.state.phone}
                    onChange={this.handleInfo} 
                    required/>
                </Form.Group>

                <Form.Group controlId="exampleForm.ControlTextarea1">
                    <Form.Label><Trans>Comment</Trans></Form.Label>
                    <Form.Control as="textarea" rows="3"
                     type="text" 
                     name="comment"
                     value={this.state.comment}
                     onChange={this.handleInfo} 
                     required/>
                </Form.Group>
                </Form> 
                <div className="text-center">
                    { this.props.userID !== null ? (
                         <button className='btn btn-rent' type='button' onClick={this.handleSubmit} data-toggle="modal" data-target="#rent">
                         <Trans>Rent</Trans>
                        </button>
                    ): 
                     <div> 
                        <Trans>
                          YouNeedTo
                        </Trans> {' '}
                        <Link to="/login">
                          <Trans>login</Trans>
                        </Link> {' '}
                        <Trans>toContinue</Trans>
                        </div>}
                </div>

                <div className="modal fade" id="rent" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">
                  <Trans>PedidoRent</Trans>
                </h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <h4 className="text-center">
                    <Trans>NumberID</Trans>: <strong>{this.state.ConfirmNum.slice(Math.max(this.state.ConfirmNum - 7, 1))}</strong>
                </h4>
                <p className="text-justify">
                  <Trans>TextParagraph</Trans>
                </p>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                {/* <button type="button" class="btn btn-primary">Save changes</button> */}
              </div>
            </div>
          </div>
        </div>
                </Container>
            </main>
        )
    }
}

const Rent = withTranslation()(RentComponent)
export default React.memo(Rent);